import React, { Component } from "react";
import {
    Button,
    Input,
    ListGroup,
    ListGroupItem,
} from "reactstrap";


class InstitutionList extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="h-100 d-flex flex-column">
                <InstitutionListHead/>
                <InstitutionListTable/>
            </div>
        );
    }
}

class InstitutionListHead extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="page-head">
                <div className="page-head-controls">
                    <Button outline color="success" size="sm" className="ml-auto">Add</Button>
                </div>
                <h4 className="page-head-title">Institutions</h4>
                <Input type="search" placeholder="Search" className="search-input"/>
            </div>
        );
    }
}

class InstitutionListTable extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="page-body">
                <ListGroup>
                    <InstitutionListRow/>
                    <InstitutionListRow/>
                    <InstitutionListRow/>
                </ListGroup>
            </div>
        );
    }
}

class InstitutionListRow extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <ListGroupItem>University Name</ListGroupItem>
        );
    }
}

export default InstitutionList;